.header__home {
  z-index: 0;
  background-color: #fffc;
  background-image: url("header-mobile.db32398b.jpg");
  background-position-y: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 40px;
}

.header__nav {
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 57px;
  display: flex;
}

.header__logo--svg {
  height: 24px;
  width: 24px;
}

.navigation__list {
  list-style: none;
  display: flex;
}

.navigation__item--link {
  text-transform: uppercase;
  color: var(--text-white);
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  position: relative;
}

.navigation__item--relative:after {
  content: "";
  background-color: var(--underline);
  height: 3px;
  width: 110%;
  position: absolute;
  bottom: -5px;
  left: -5%;
}

.additional-padding {
  padding-left: 40px;
}

.form__search {
  justify-content: center;
  padding-bottom: 92px;
  display: flex;
  position: relative;
}

.form__search--input {
  width: 280px;
  color: var(--text-white);
  background-color: initial;
  cursor: pointer;
  border: none;
  border-bottom: .5px solid #fff;
  padding-bottom: 4px;
  padding-right: 40px;
}

.form__search--input::placeholder {
  color: var(--text-white);
  font-family: Roboto;
  font-size: 14px;
  line-height: 16px;
}

.form__search--input:focus-visible {
  outline: 0;
}

.form__btn {
  background-color: initial;
  cursor: pointer;
  border: none;
  padding: 0;
  transition: transform .25s cubic-bezier(.075, .82, .165, 1);
  position: absolute;
  left: 76%;
}

.form__btn:hover {
  transform: scale(1.5);
}

.form__btn:focus-visible {
  outline: 0;
}

.form__btn--icon {
  height: 12px;
  width: 12px;
  height: 100%;
}

:root {
  --background-footer: #f7f7f7;
  --background-body: #e5e5e5;
  --text-red: #ff001b;
  --text-white: #fff;
  --text-black: #000;
  --text-orange: #ff6b08;
  --text-footer: #545454;
  --underline: #ff001b;
}

.container {
  width: 90%;
  margin: 0 auto;
  padding-inline: 0;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

body {
  font-family: Roboto, sans-serif;
}

